  ngOnInit() {
    this.route.events.subscribe((event) => {
      // Por causa do aviso de "Navigation triggered outside Angular zone"
      // Existia bug na detectao de mudanas em algumas navegaes
      // Isso conserta esses bugs.
      if (event instanceof NavigationEnd) {
        this.ref.detectChanges()
      }
      // NavigationStart
      // NavigationEnd
      // NavigationCancel
      // NavigationError
      // RoutesRecognized
    })
    this.initializeApp()
    //this.setRootPage();
    this.nanoPush.init()
    this.menuCtrl.enable(false)
  }
  /**
   * Inicializar platform e plugins nativos
   */
  private initializeApp(): void {
    this.platform.ready().then(() => {
      if (this.platform.is('cordova')) {

        if (this.platform.is('ios')) {
          this.statusBar.overlaysWebView(false);
        }

        // this.statusBar.styleDefault();
        this.statusBar.styleLightContent();
        // this.statusBar.backgroundColorByHexString('#3AAA35');
        this.statusBar.backgroundColorByHexString('#D32F2F');
        this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
        this.hideSplashScreen();
      }
    });
  }

  hideSplashScreen() {
    if (this.splashScreen) {
      setTimeout(() => {
        this.splashScreen.hide();
      }, 5); //delay de esconder splashscreen ps plataforma ser carregada
    }
  }

