import { CategoryProduct } from './relationships/category-product';
import { Collection } from '../modules/firestore/collection';
import { FilialStock } from './relationships/filial-stock';
import { RelationshipField } from '../modules/firestore/collection-relationship';
import { Category } from './category';
import { Filial, FilialType } from './filial';
import { Company } from './company';
import { CompanyProduct } from './relationships/company-product';

export class Product extends Collection {

    static path = "products";

    // Propriedades de TODOS os relacionamento da entidade
    public static get company() {
        return new CompanyProduct<Company>(Product)
    }

    public static get category() {
        return new CategoryProduct<Category>(Product)
    }

    public static get filial() {
        return new FilialStock<Filial>(Product)
    }

    // Relacionamentos One
    company: RelationshipField
    category: RelationshipField
    filial: RelationshipField

    // Persistentes no Firebase
    name?: string;
    description?: string;
    image?: string;
    // Existe apenas em produtos globais
    global?: {
        type?: FilialType
    }
    minimumPrice?: number; //preo base + soma de preo de todos os complementos obrigatrios
    priceBase?: number; //preo inicial do produto (sem considerar complementos)
    promotionActive?: boolean;
    promotionPrice?: number;
    promotionPercentage?: number;
    priority?: number;
    fixedQuantity?: boolean;
    complements?: Complement[];

    /**
     * Calcula o price(preo atual mnimo) de um produto.
     * O preo atual  o preo base mais o preo mnimo gasto em todos os complementos obrigatrios.
     * Logo o price  o mnimo a ser gasto ao comprar esse produto.
     * @param {Product} p Produto no qual ser calculado o price(preo atual mnimo)
     */
    static calculatePrice(p: Product): Product {
        p.minimumPrice = p.priceBase || 0;
        p.complements.forEach((complement: Complement) => {
            let minimumPriceComplement = 0;
            if (complement.options && complement.options.length > 0) {
                // Mnimo de opes obrigatrias
                let min: number = 0
                switch (complement.type) {
                    // Se tipo single o mnimo escolhido  1 apenas se ele for required.
                    case ComplementType.single:
                        min = (complement.required ? 1 : 0)
                        break
                    // Tipo multiple e quantity tem que o mnimo escolhido diretamente.
                    case ComplementType.multiple:
                    case ComplementType.quantity:
                        min = complement.min
                        break
                    default:
                        throw Error("Tipo de complemento invlido")
                }
                // Se no possui opes obrigatrias
                if (min == 0) return

                // Cria uma cpia das opes do complemento para poder editar o array
                let options: ComplementOption[] = JSON.parse(JSON.stringify(complement.options))
                // Ordenar as opes pelo preo
                options = options.sort((a: ComplementOption, b: ComplementOption) => {
                    return a.price - b.price
                })
                switch (complement.type) {
                    case ComplementType.single:
                    case ComplementType.quantity:
                        // Preo do complemento e multiplica pela quantidade mnima necessria
                        minimumPriceComplement = options[0].price * min
                        break
                    case ComplementType.multiple:
                        // Com a quantidade mnima necessria, soma os min primeiros elementos do array
                        // que est ordenado por preo
                        minimumPriceComplement = options.slice(0, min)
                            .map((a) => a.price)
                            .reduce((a, b) => a + b, 0)
                        break
                    default:
                        throw Error("Tipo de complemento invlido")
                }
            }

            p.minimumPrice += minimumPriceComplement;
        });
        return p;
    }
}

/**
 * Um complemento de algum produto.
 * Pode ser de 3 tipos:
 * 1. Escolher apenas uma opo
 * 2. Escolhar vrias opes
 * 3. Escolhar uma quantidade para cada opo(podendo essa quantidade ser 0 ou maior)
 */
export interface Complement {
    // Persistentes no Firebase
    title?: string;
    required?: boolean;
    type?: ComplementType;
    min?: number; //qtd mnima (somente para type quantity)
    max?: number; //qtd mxima (somente para type quantity)
    options?: ComplementOption[]; //se selecionada ter quantity > 0, seno quantity == 0
    resume?: string[];
}

/**
 * Tipos do complemento
 * Pode ser de 3 tipos:
 * 1. single = Escolher apenas uma opo
 * 2. multiple = Escolhar vrias opes
 * 3. quantity = Escolhar uma quantidade para cada opo(podendo essa quantidade ser 0 ou maior)
 */
export enum ComplementType {
    single = "single",
    multiple = "multiple",
    quantity = "quantity"
}

/**
 * Uma opo do complemento.
 */
export interface ComplementOption {
    name?: string;
    description?: string;
    price?: number;
    quantity?: number;
    active?: boolean;
    isChecked?: boolean;
}
