import { Injectable } from '@angular/core';
import { Log } from '../../models/log';
import { Platform, AlertController } from '@ionic/angular';
import { firestore } from "firebase";
import "firebase/firestore";

@Injectable({
  providedIn: 'root'
})
export class SystemService {

  constructor(public alertCtrl: AlertController,
    public platform: Platform) { }

  public async showNanoErrorAlert(error: NanoError) {
    if (error.report) {
      this.logEvent("nanoerror", {
        name: error.message
      })
      let log: Log = {
        date: firestore.FieldValue.serverTimestamp(),
        description: JSON.stringify(error.message) || "Stringfy no funcionou",
        origin: "nanoerror",
        title: JSON.stringify(error.title) || "Sem title"
      };
      Log.add(log).subscribe()
    }

    let alert = await this.alertCtrl.create({
      header: error.title,
      subHeader: error.message,
      buttons: ['OK']
    });
    alert.present();
  }

  public async showAlert(header, subHeader, message): Promise<void> {

    let obj: any = {};

    obj.buttons = ['OK'];

    if (header) obj.header = header;
    if (subHeader) obj.subHeader = subHeader;
    if (message) obj.message = message;
    let alert = await this.alertCtrl.create(obj)
    alert.present();

  }

  public async showErrorAlert(error?, origin?: string, log_backend?: boolean) {
    if (error.nanoerror) return this.showNanoErrorAlert(error)
    var know_issue: boolean = false
    console.error("Ocorreu um erro: ", error);

    if (origin && origin == 'logout') return;

    //Mensagem genrica para erro desconhecido ou no mapeado
    let title = 'Falha de comunicao!';
    let msg = 'Ocorreu uma falha de comunicao! Tente novamente mais tarde.';

    if (error && error.message) {
      title = "Ops!";
      msg = error.message;
    }
    else if (error) {
      title = "Ops!";
      msg = JSON.stringify(error);
    }

    if (error && error.title) {
      title = error.title;
    }

    if (msg == 'Missing or insufficient permissions.') {
      know_issue = true
      title = "Permisso invlida"
      msg = "Usurio no tem permisso para realizar essa ao! Por favor contate o administrador."
    }

    //Tratamento com base no cdigo de erros especficos
    if (error && error.code == 'auth/account-exists-with-different-credential') {
      know_issue = true
      title = 'Conta j existe'
      msg = 'Uma conta j existe com o mesmo endereo de e-mail, mas credenciais de login diferentes. Faa login usando um provedor associado a este endereo de e-mail.'
    }
    else if (error && error.code == 'auth/popup-closed-by-user') {
      know_issue = true
      title = 'Erro de autenticao';
      msg = 'Janela fechada pelo usurio antes de concluir a autenticao. Tente novamente.'
    }
    else if (error && error.code == 'auth/email-already-exists') {
      know_issue = true
      title = 'Email j cadastrado';
      msg = 'Essa conta de email j foi cadastrada no painel administrativo ou no aplicativo. Por favor, escolha outro email e tente novamente.'
    }

    /**
     * Se no  um problema conhecido e deve avisar o backend
     */
    if (!know_issue && log_backend) {
      let log: Log = {
        date: firestore.FieldValue.serverTimestamp(),
        description: JSON.stringify(error) || "Stringfy no funcionou",
        description_error: JSON.stringify(error.msg) || "Sem msg",
        origin: origin || "Sem origem",
        title: JSON.stringify(error.title) || "Sem title"
      };
      Log.add(log).subscribe()
    }

    let alert = await this.alertCtrl.create({
      header: title,
      subHeader: msg,
      buttons: ['OK']
    });
    alert.present();

  }


  setScreenName(name: string) {
    // if (this.platform.is('cordova')) {
    //   this.firebase.setScreenName(name).then((data) => {
    //   })
    // }
  }

  logEvent(name: string, data?: any) {
    // if (this.platform.is('cordova')) {
    //   this.firebase.logEvent(name, data || {}).then((data) => {
    //   })
    // }
  }

  setUserId(userId) {
    // if (this.platform.is('cordova')) {
    //   this.firebase.setUserId(userId).then((data) => {
    //   })
    // }
  }

  setUserProperty(name?: string, data?: any) {
    // if (this.platform.is('cordova')) {
    //   this.firebase.setUserProperty(name, data || {}).then((data) => {
    //   })
    // }
  }

}

/**
 * Interface com erros controlados pela nanocode.
 * Contem tradues dos erros de diversos plugins
 */
export interface NanoError {
  nanoerror: true,
  title: string,
  message: string,
  report: boolean // Se deve ser registrado no log ou no
}
