import { CardFoodType, CardType, Filial } from "./filial";
import { Collection } from "../modules/firestore/collection";
import { Company } from "./company";
import { CompanyOrders } from "./relationships/company-orders";
import {
  Complement,
  ComplementOption,
  ComplementType,
  Product,
} from "./product";
import { CouponOrder } from "./coupon";
import { FilialOrders } from "./relationships/filial-orders";
import { firestore } from "firebase";
import { Location } from "./utils/location";
import { RelationshipField } from "../modules/firestore/collection-relationship";
import { User } from "./user";
import { UserOrders } from "./relationships/user-orders";
import "firebase/firestore";

/**
 * ORDER (Pedido)
 * Composto por uma lista de itens + informaes extras do pedido
 */
export class Order extends Collection {
  static path = "orders";

  // Propriedades de TODOS os relacionamento da entidade
  public static get company() {
    return new CompanyOrders<Company>(Order);
  }

  public static get filial() {
    return new FilialOrders<Filial>(Order);
  }

  public static get user() {
    return new UserOrders<User>(Order);
  }

  entries?: Entry[];
  observation?: string;

  //Cdigo nico exibido no painel administrativo
  code?: string;

  //Cupom utilizado no pedido
  coupon?: CouponOrder;

  //Momento de cada processo
  created_at?: firestore.Timestamp | firestore.FieldValue;
  accepted_at?: firestore.Timestamp | firestore.FieldValue;
  doing_at?: firestore.Timestamp | firestore.FieldValue;
  delivery_at?: firestore.Timestamp | firestore.FieldValue;
  done_at?: firestore.Timestamp | firestore.FieldValue;
  refused_at?: firestore.Timestamp | firestore.FieldValue;
  away_at?: firestore.Timestamp | firestore.FieldValue;
  incomplete_at?: firestore.Timestamp | firestore.FieldValue;
  cancellation_reason?: string;

  payment_method?: PaymentMethod;
  payment_cardType?: CardType | CardFoodType;
  change?: string;
  status?: OrderStatus;
  location?: Location;

  price?: {
    delivery?: number; //taxa de entrega
    subtotal?: number; //Soma do valor de todos os produtos
    discounts?: number;
    total?: number; //delivery + subtotal (o que o consumidor efetivamente deve pagar a empresa)
  };

  company?: RelationshipField;
  filial?: RelationshipField;
  user?: RelationshipField;

  // No persistentes no DB
  filial_full?: Filial;
  user_full?: User;

  //Avaliao do usurio do pedido
  evaluate?: {
    rate?: number; //Nota de satisfao do pedido fornecida por um usurio (1 a 5)
    comment?: string;
    created_at?: firestore.Timestamp;
  };
}

/**
 * Item do carrinho composto de um produto + informaes extra do item
 */
export class Entry {
  //Produto referente ao item
  product?: Product;

  //Quantidade da entrada
  quantity?: number;

  //Complementos referente  entry
  complements?: Complement[];

  //Preo real da entrada, incluindo os complementos
  price?: number;

  // Ex: 2x Bacon, R$ 2,90
  complement_resume?: string[];

  //Verifica se todos complementos obrigatrios foram escolhidos (frontend)
  valid?: boolean;

  //Observaes do pedido
  observation?: string;

  /**
   * Gera uma entry a partir de um produto
   * @param product Produto que ser usado para gerar uma entry
   * @todo No est copiando o objeto produto direito. Logo quando  feita uma edio
   * na quantidade de uma opo de um complemento, ela persiste no objeto original do produto.
   */
  static entryFromProduct(product: Product): Entry {
    let entry: Entry = {};
    entry.product = JSON.parse(JSON.stringify(product));
    entry.quantity = 1;
    entry.complements = entry.product.complements;
    return entry;
  }
  /**
   * Calcula os valores agregados de uma entry basedo nas opes escolhidas dos complementos e valor base.
   * Calcula-se o Preo, Resumo da entry, e se  uma entry vlida ou no
   * @param entry entry que ser usada para calcular
   */
  static calculateEntryValues(entry?: Entry): void {
    // Inicializado com o preo base.
    let price: number = entry.product.priceBase;

    // Array de strings no qual cada string representa o resumo de uma opo escolhida do complemento.
    let complement_resume: string[] = [];

    // Todo pedido  vlido at que se prove o contrrio
    let valid: boolean = true;

    // Para todos os complementos.
    (entry.complements || []).forEach((complement: Complement) => {
      if (
        complement.type !== ComplementType.single &&
        complement.type !== ComplementType.multiple &&
        complement.type !== ComplementType.quantity
      ) {
        console.error("Tipo de complemento invlido");
        throw Error("Essa entrada possui um tipo de complemento invlido");
      }

      // Inicializa o resumo do complemento.
      complement.resume = [];
      let quantity_sum_options = 0;
      // Para cada opo
      (complement.options || []).forEach((option: ComplementOption) => {
        // Se a opo tiver sido escolhida.
        if (option.quantity && option.quantity > 0) {
          quantity_sum_options += option.quantity;
          // Preo da opo * quantidade da opo.
          let price_option = option.price * option.quantity;
          if (option.price > 0) {
            // Adiciona no preo do pedido.
            price += price_option;
            // Adiciona a string relativa a opo do pedido.
            complement.resume.push(
              `${option.quantity}x ${option.name},  R$ ` +
                price_option.toLocaleString("pt-BR", {
                  minimumIntegerDigits: 1,
                  minimumFractionDigits: 2,
                  maximumFractionDigits: 2,
                  useGrouping: false,
                })
            );
          } else {
            // Adiciona a string relativa a opo do pedido.
            complement.resume.push(`${option.quantity}x ${option.name}`);
          }
        }
      });

      switch (complement.type) {
        case ComplementType.single:
          if (quantity_sum_options == 0 && complement.required) {
            valid = false;
          }
          break;
        case ComplementType.multiple:
        case ComplementType.quantity: {
          if (quantity_sum_options < complement.min) valid = false;
          else if (complement.max == 0) valid = true;
          else if (quantity_sum_options > complement.max) {
            valid = false;
          }
          break;
        }
      }
      // Concatena o resumo do complmento com o resumo do pedido.
      complement_resume = complement_resume.concat(complement.resume);
    });

    entry.price = price;

    //Ajustar valor da entrada caso o produto esteja em promoo
    if (entry.product.promotionActive) {
      if (entry.product.promotionPrice) {
        entry.price = entry.price - entry.product.promotionPrice;
      } else if (entry.product.promotionPercentage) {
        entry.price =
          ((100 - entry.product.promotionPercentage) / 100) * entry.price;
      }
    }

    entry.complement_resume = complement_resume;
    entry.valid = valid;
  }
}

/**
 * Tipos do complemento
 * Pode ser de 3 tipos:
 *  usado os nmeros representando o status do pedido no bd pois  usado para ordenar
 * 0. created = Pedido foi solicitado pelo usurio.
 * 1. accepted = Pedido foi aceito pela empresa.
 * 2. doing = Pedido est sendo feito.
 * 3. delivery = Pedido saiu para entrega.
 * 4. done = Pedido foi entregue ao usurio.
 * 5. away = O pedido foi cancelado por falta de resposta.s
 * 6. refused = Pedido foi recusado pela empresa.
 * 7. incomplete = O pedido foi aceito. Mas aps isso no foi completado
 */
export enum OrderStatus {
  created,
  accepted,
  doing,
  delivery,
  done,
  away,
  refused,
  incomplete,
}

/**
 * Mtodos de pagamento
 * 0. auxCard = auxiliar que identifica se foi selecionado carto de crdito ou dbito
 * 1. credit_card = Pagamento realizado via crdito
 * 2. debit_card = Pagamento realizado via dbito
 * 3. money = Pagamento realizado em dinheiro
 * 4. food_stamps = Pagamento realizado por vale-alimentao
 */
export enum PaymentMethod {
  auxCard = "card", //AUXILIAR PARA SABER QUE FOI SELECIONADO CARTO
  credit_card = "credit_card", //carto de crdito
  debit_card = "debit_card", //carto de dbito
  money = "money", //dinheiro
  food_stamps = "food_stamps", //vale-alimentao
}
